window.$ = window.jQuery = function (selectorOrArrayOrTemplate) {
  //Êé•Êî∂ÈÄâÊã©Âô®ÊàñËÄÖÊï∞ÁªÑ
  let elements;
  if (typeof selectorOrArrayOrTemplate === "string") {
    elements = document.querySelectorAll(selectorOrArrayOrTemplate);
    //jQuery ËøîÂõûÁöÑÊòØ‰∏Ä‰∏™ÂØπË±°ÔºåÂèØ‰ª•Êìç‰ΩúelementsÁöÑapi
    //api ÂèØ‰ª•Êìç‰Ωúelements
  } else if (selectorOrArrayOrTemplate instanceof Array) {
    elements = selectorOrArrayOrTemplate;
  }

  const api = Object.create(jQuery.prototype); //ÂàõÂª∫‰∏Ä‰∏™ÂØπË±°ÔºåËøô‰∏™ÂØπË±°ÁöÑ__proto__‰∏∫Êã¨Âè∑ÈáåÁöÑ‰∏úË•ø
  // Á≠â‰ª∑‰∫é const api ={__proto__:jQuery.prototype}
  //   api.elements = elements;
  //   api.oldApi = selectorOrArray.oldApi;
  Object.assign(api, {
    elements: elements,
    oldApi: selectorOrArrayOrTemplate.oldApi,
  }); //Êää‰∏≠Êã¨Âè∑ÈáåÈù¢ÁöÑÂ±ûÊÄßÔºå‰∏Ä‰∏™‰∏Ä‰∏™Â§çÂà∂api‰∏≠ÔºåÊµÖÂ§çÂà∂
  return api;
};

jQuery.fn = jQuery.prototype = {
  //jQueryÂØπÂÆÉÁöÑprototypeÂèñ‰∫Ü‰∏Ä‰∏™Âà´ÂêçÂè´fn
  constructor: jQuery,
  jquery: true,
  get(index) {
    return this.elements[index]; //Áî®this‰Ωú‰∏∫Ê°•Ê¢ÅÂéªËÆøÈóÆelements
  },
  find(selector) {
    let array = [];
    for (let i = 0; i < this.elements.length; i++) {
      const elements2 = Array.from(this.elements[i].querySelectorAll(selector));
      array = array.concat(elements2); //elements‰∏≠ÊúâÂ§ö‰∏™testÔºåË¶Å‰æùÊ¨°ËøõÂÖ•ÈÅçÂéÜ
    }
    array.oldApi = this; //this ÊòØ Êóßapi
    const newApi = jQuery(array); //jQueryÂàõÂª∫‰∏Ä‰∏™Êñ∞ÁöÑapi
    return newApi;
    //‰∏çËÉΩÁõ¥Êé•return
  },

  each(fn) {
    for (let i = 0; i < this.elements.length; i++) {
      fn.call(null, this.elements[i]);
    }
    return this;
  },
  parent() {
    const array = [];
    this.each((node) => {
      //thisÂ∞±ÊòØapi
      if (array.indexOf(node.parentNode) === -1) {
        array.push(node.parentNode); //Ê≤°ÊúâÁà∂‰∫≤Â∞±pushÔºåÊúâÂ∞±‰∏çpush‰∫ÜÔºåÂõ†‰∏∫ÊúâÂá†‰∏™testÔºåÂè™ÊâìÂç∞Âá∫‰∏Ä‰∏™Â∞±Ë°å
      }
    });
    return jQuery(array); //ËøîÂõûÂèØ‰ª•Êìç‰ΩúÊï∞ÁªÑÁöÑÂØπË±°
  },
  print() {
    console.log(this.elements); //elementsÂ∞±ÊòØÂØπÂ∫îÂÖÉÁ¥†‰ª¨
  },

  appendTo(node) {
    if (node instanceof Element) {
      this.each((el) => node.appendChild(el)); // ÈÅçÂéÜ elementsÔºåÂØπÊØè‰∏™ el ËøõË°å node.appendChild Êìç‰Ωú
    } else if (node.jquery === true) {
      this.each((el) => node.get(0).appendChild(el)); // ÈÅçÂéÜ elementsÔºåÂØπÊØè‰∏™ el ËøõË°å node.get(0).appendChild(el))  Êìç‰Ωú
    }
  },
  append(children) {
    if (children instanceof Element) {
      this.get(0).appendChild(children);
    } else if (children instanceof HTMLCollection) {
      for (let i = 0; i < children.length; i++) {
        this.get(0).appendChild(children[i]);
      }
    } else if (children.jquery === true) {
      children.each((node) => this.get(0).appendChild(node));
    }
  },

  children() {
    const array = [];
    this.each((node) => {
      // ‰∏äËØæÁöÑÊó∂ÂÄôËøôÊÆµ‰ª£Á†ÅÊòØÂ§çÂà∂ÁöÑÔºåÂ§çÂà∂Èîô‰∫ÜÔºåÁé∞Â∑≤ÊîπÊ≠£
      array.push(...node.children);
      //Á≠â‰ª∑‰∫é array.push(node.children[0],node.children[1],node.children[2],....)ÊääÈáåÈù¢ÁöÑÂÖÉÁ¥†ÂºÑÂπ≥
    });
    return jQuery(array);
  },

  // addClass: function () {console.log(elements);},
  addClass(className) {
    //Èó≠ÂåÖÔºöÂáΩÊï∞ËÆøÈóÆÂ§ñÈÉ®ÂèòÈáèÔºåËøôÈáåaddClassËÆøÈóÆ‰∫Üelements
    //Áªô‰∏Ä‰∏™classNameÔºå‰ºöÈÅçÂéÜÊï¥‰∏™elements
    for (let i = 0; i < this.elements.length; i++) {
      elements[i].classList.add(className); //ÊØè‰∏™elementÊ∑ªÂä†‰∏Ä‰∏™className
    }
    return this;
  },
  end() {
    return this.oldApi; //Ê≠§Êó∂ÁöÑthisÊòØ Êñ∞ÁöÑapi
  },
};
